	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"https://jsfiddle.net\">JSFiddle</a> just announced support for React. This is an exciting news as it makes collaboration on snippets of code a lot easier. You can play around this <strong><a href=\"http://jsfiddle.net/vjeux/kb3gN/\">base React JSFiddle</a></strong>, fork it and share it! A <a href=\"http://jsfiddle.net/vjeux/VkebS/\">fiddle without JSX</a> is also available.</p>\n<blockquote class=\"twitter-tweet\" align=\"center\"><p>React (by Facebook) is now available on JSFiddle. <a href=\"http://t.co/wNQf9JPv5u\" title=\"http://facebook.github.io/react/\">facebook.github.io/react/</a></p>&mdash; JSFiddle (@jsfiddle) <a href=\"https://twitter.com/jsfiddle/status/341114115781177344\">June 2, 2013</a></blockquote>","excerpt":"JSFiddle  just announced support for React. This is an exciting news as it makes collaboration on snippets of code a lot easier. You can play around this  base React JSFiddle , fork it and share it! A  fiddle without JSX  is also available.","frontmatter":{"title":"JSFiddle Integration","next":null,"prev":null,"author":[{"frontmatter":{"name":"Vjeux","url":"https://twitter.com/vjeux"}}]},"fields":{"date":"June 01, 2013","path":"blog/2013-06-02-jsfiddle-integration.md","slug":"/blog/2013/06/02/jsfiddle-integration.html"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"JSFiddle Integration"},"fields":{"slug":"/blog/2013/06/02/jsfiddle-integration.html"}}}]}},"pathContext":{"slug":"/blog/2013/06/02/jsfiddle-integration.html"}}

